.post-link:hover {
  // border-top: 6px double $black;
  border-bottom: 3px solid $red;
  -o-transition:.5s;
  -ms-transition:.5s;
  -moz-transition:.5s;
  -webkit-transition:.5s;
  /* ...and now for the proper property */
  transition:.5s;
}
